.main__container-delivery {
    position: relative;
    height: 125px;
    padding-top: 30px;
}
.container-delivery__column {
    height: 125px;
    position: absolute;
    width: 50%;
    left: 0;
    background-color: $main-delivery-column-color-left;
}
.container-delivery__column_color {
    left: 50%;
    background-color: $main-delivery-column-color-right;
}
.container-delivery__container-bg {
    width: 1005px;
    background-color: $main-delivery-column-color-right;
    height: 125px;
}
.container_container-delivery {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 125px;
}
.container-delivery__bg-car {
    position: relative;
    width: 143px;
    height: 61px;
    background: url('../img/sprite.png') -510px -170px no-repeat;
}
.container-delivery__bg-car::after {
    content: '';
    display: block;
    position: absolute;
    right: -90px;
    top: -32px;
    border: 63px solid transparent;
    border-left: 25px solid $main-delivery-column-color-left;
}
.container-delivery__description {
    float: right;
    font-family: 'Noto Sans', sans-serif;
    font-size: 28px;
    line-height: 32px;
    color: $main-delivery-description-color-text;
}
.button_delivery {
    float: right;
    border-color: $main-delivery-description-color-text;
    color: $main-delivery-description-color-text;
    font-size: 16px;
    line-height: 65px;
    width: 290px;
    margin: 28px 0;
    margin-left: 60px;
    border-radius: 35px;
}