.main__make-request {
    background-color: $main-make-request-bg-color;
    font-family: 'Noto Sans', sans-serif;
    padding-top: 75px;
}
.container_make-request {
    position: relative;
    padding-bottom: 50px;
}
.button_make-request {
    position: absolute;
    right: 0;
    bottom: -32.5px;
}
.make-request__request-form {
    width: 100%;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}
.request-form__container-request-form {
    width: 555px;
    box-sizing: border-box;
}
.container-request-form__entry-field {
    box-sizing: border-box;
    width: 555px;
    border: 1px solid $main-entry-field-border-color;
    line-height: 50px;
    padding: 0 45px;
    outline: none;
}
.container-request-form__entry-field_size {
    width: 460px;
}
.container-request-form__entry-field-label_float_left {
    float: left;
}
.container-request-form__entry-field_size_small {
    width: 80px;
    float: right;
    padding: 0 15px;
}
.container-request-form__entry-field_textarea {
    height: 254px;
    resize: none;
}
.container-request-form__entry-field-label {
    position: relative;
    line-height: 52px;
    margin-bottom: 15px;
    display: inline-block;
}
.container-request-form__entry-field-icon {
    position: absolute;
    width: 35px;
    height: 52px;
    top: 0;
    left: 0;
}
.container-request-form__entry-field-icon_fullname {
    background: url('../img/sprite.png') -16px -317px no-repeat;
}
.container-request-form__entry-field-icon_tel {
    background: url('../img/sprite.png') -60px -317px no-repeat;
}
.container-request-form__entry-field-icon_mail {
    background: url('../img/sprite.png') -106px -317px no-repeat;
}
.container-request-form__entry-field-icon_component {
    background: url('../img/sprite.png') -152px -317px no-repeat;
}
.container-request-form__entry-field-icon_comments {
    background: url('../img/sprite.png') -195px -317px no-repeat;
}
