.main__news {
    padding-bottom: 115px;
    padding-top: 115px;
    font-family: 'Noto Sans', sans-serif;
}
.container__news {
    display: flex;
    justify-content: space-between;
    padding-bottom: 50px;
}
.news__shell-news {
    width: 263px;
    box-sizing: border-box;
}
.shell-news__data-post-news {
    color: $main-news-data-post-color-text;
    margin-top: 3px;
    display: inline-block;
    font-size: 12px;
    line-height: 30px;
}
.shell-news__title-news {
    color: $main-news-color-text;
    margin: 0;
    font-size: 14px;
    line-height: 20px;
}
.shell-news__content-news {
    letter-spacing: -1px;
    color: $main-news-color-text;
    line-height: 18px;
    font-size: 12px;    
}
.news__shell-news_main {
    position: relative;
    padding: 230px 20px 25px 20px;
    width: 555px;
}
.shell-news__img-news_main {
    top: 0;
    left: 0;
    position: absolute;
    z-index: 1;
}
.shell-news__data-post-news_main {
    position: relative;
    z-index: 5;
    font-size: 14px;
    line-height: 38px;
    padding: 0 15px;
    background-color: $main-news-active-bg-color;
    color: $main-news-active-text-color;
}
.shell-news__title-news_main {
    position: relative;
    z-index: 5;
    font-size: 18px;
    line-height: 24px;
    color: $main-news-active-text-color;
}
.shell-news__content-news_main {
    letter-spacing: 0;
    position: relative;
    z-index: 5;
    font-size: 14px;
    line-height: 20px;
    color: $main-news-active-text-color;
}
.button_pos_news {
    margin: 0 auto;
}
