.footer {
    min-width: 1250px;
    font-family: 'Noto Sans', sans-serif;
    background-color: $footer-bg-color;
}
.container_pos_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 35px;
}
.nav__link_active_footer::after {
    content: none !important;
}
.button_pos_footer {
    float: right;
}
.communication_footer {
    display: flex;
    justify-content: space-between;
    width: 355px;
    max-width: 355px;
}
.communication__element_email_footer {
    width: 164px;
}
.footer__simplified-logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 335px;
}
.container_padding_small {
    padding-top: 25px;
    padding-bottom: 60px;
}
.simplified-logo__bg {
    min-width: 64px;
    height: 67px;
    background: url('../img/footer-logo.png') 50% 50% no-repeat;
}
.simplified-logo__text {
    font-size: 14px;
    line-height: 20px;
    color: $footer-color-text;
    letter-spacing: -1px;
}

