.main__container-row-communication {
    padding-top: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.main__logo {
    width: 270px;
    height: 65px;
    background: url('../img/logo.png') 50% 50% no-repeat;
    background-size: contain;
}
.search__entry-field {
    outline: none;
    width: 460px;
    line-height: 40px;
    border: none;
    border-radius: 25px;
    font-family: 'Noto Sans', sans-serif;
    font-size: 16px;
    padding-left: 20px;
    background: $main-color url('../img/search.png') 95% 50% no-repeat;
}

