
body, html {
	margin: 0;
	padding: 0;
	background-color: #ffffff;
}
.container {
	position: relative;
	width: 1150px;
	margin: 0 auto;
}
.clearfix::after {
	content: "";
	display: table;
	clear: both;
}

@media (max-width: 425px) {
    .container {
        max-width: 425px;
    }
}
