.nav {
    float: left;
}
.nav__link {
    position: relative;
    text-decoration: none;
    font-size: 16px;
    line-height: 50px;
    color: $nav-color-default;
    display: inline-block;
    float: left;
    margin-right: 25px;
}
.nav__link::before {
    content: none;
}
.nav__link:hover::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 3px;
    background-color: $nav-color-default;
}
.nav__link_active:hover::before {
    content: none;
}
.nav__link_active::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 3px;
    background-color: $nav-color-active;
}
.nav__link_active {
    color: $nav-color-active;
}
.switch-language {
    float: right;
    display: flex;
    align-items: center;
    height: 50px;
}
.switch-language__tongue {
    cursor: pointer;
    font-size: 13px;
    text-transform: uppercase;
    display: block;
    float: left;
    border-radius: 50%;
    margin-left: 5px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    text-align: center;
    background-color: $nav-color-tongue-default-bg;
    color: $nav-color-tongue-default-text;
}
.switch-language__tongue_active {
    background-color: $nav-color-tongue-active-bg;
    color: $nav-color-tongue-active-text;
}
.switch-language_mob-show {
    display: none;
}
.container_scroll-nav {
    width: 100% ;
    position: fixed ;
    left: 0;
    top: 0; 
    background-color: $header-color;
}
.communication_scroll {
    display: flex;
    justify-content: space-between;
    width: 333px ;
    max-width: 355px ;
}
.container_scroll_pos {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.button_scroll {
    width: 182px;
}
.nav__link_font-size_smal {
    margin-right: 10px;
    font-size: 14px;
}
.nav__link_margin-right {
    margin-right: 34px;
}
.communication__element_scroll {
    font-size: 15px;
    width: 163px;
}
.communication__element_email_scroll {
    width: 154px;
}
.button_nav_header {
    display: none;
}
.header_mob__communication {
    display: none;
}