.main__make-request_popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 1250px;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 15;
}
.container_make-request_popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-bottom: 90px;
    padding-top: 30px;
    box-sizing: border-box;
    background-color: #fff;
    width: 775px;
}
.make-request__close {
    width: 13px;
    height: 13px;
    background: url('../img/cross.png') 50% 50% no-repeat;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
.request-form__container-request-form_popup {
    width: 555px;
  
}
.title_popup {
    padding-left: 111px;
    color: $popup-title-color;
    font-size: 36px;
}
.make-request__request-form_popup {
    position: relative;
    padding-top: 35px;
    justify-content: center;
    padding-bottom: 30px;
    background: url('../img/popup-bg.jpg') 50% 50% no-repeat;
    background-size: cover;
}
.container-request-form__entry-field_textarea_popup {
    height: 150px;
}
.button_popup {
    right: 109px;
}
