.main__work-process {
    display: flex;
    justify-content: space-between;
}
.work-process__column-work-process {
    width: 50%;
    box-sizing: border-box;
    font-family: 'Noto Sans', sans-serif;
}
.title-appellation {
    font-size: 20px;
    color: $main-title-color;
}
.title {
    font-size: 44px;
    margin: 10px 0;
    margin-bottom: 35px;
    color: $main-title-color;
}
.column-work-process___description {
    font-size: 14px;
    line-height: 26px;
    margin-bottom: 20px;
}
.work-process-list__paragraph {
    position: relative;
    font-size: 15px;
    line-height: 22px;
    padding: 10px 0;
    padding-left: 45px;
}
.work-process-list__paragraph::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 40px;
    height: 30px;
    background: url('../img/sprite.png') no-repeat;
}
.work-process-list__paragraph_payment::after {
    background-position: -357px -35px;
}
.work-process-list__paragraph_minimum::after {
    background-position: -357px -81px;
}
.work-process-list__paragraph_discounts::after {
    background-position: -355px -127px;
} 
.work-process-list__paragraph_delivery::after {
    background-position: -352px -170px;
} 
.work-process-list__paragraph_missing::after {
    background-position: -352px -216px;
}  
.column-work-process__work-process-list {
    list-style-type: none;
    padding: 0;
}
.work-process__column-work-process_bg {
    background: url('../img/work-process-bg.jpg') 50% 50% no-repeat;
}