.communication {
    text-align: right;
    max-width: 175px;
}
.communication__element {
    display: inline-block;
    max-width: 175px;
    width: 175px;
    position: relative;
    color: $main-color;
    font-family: 'Noto Sans', sans-serif;
    font-size: 16px;
    line-height: 27px;
}
.communication__element_tel::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 35px;
    height: 27px;
    background: url('../img/sprite.png') -32px -31px no-repeat;
}
.communication__element_email::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 35px;
    height: 27px;
    background: url('../img/sprite.png') -76px -31px no-repeat;
}
