@media (max-width: 425px) {
    .header {
        position: fixed;
        top: 0;
        left: 0;
        max-width: 425px;
        min-width: 320px;
    }
    .container__header_mob {
        height: 45px;
        display: flex;
        padding: 0 15px;
        justify-content: space-between;
        box-sizing: border-box;
        align-items: center;
    }
    .container__header_mob::after {
        content: none;
    }
    .header_mob__communication {
        display: block;
    }
    .header_mob__communication__element_tel {
        font-size: 14px;
        text-decoration: none;
    }
    .header_mob__communication__element_tel::after {
        background: none;
    }
    .header__mob-logo {
        width: 125px;
        height: 29px;
        background: url('../img/mob-logo.png') 50% 50% no-repeat;
    }
    .header__burger {
        width: 21px;
        height: 15px;
        background: url('../img/sprite.png') -513px -43px no-repeat;
    }
    .nav_header__cross {
        position: absolute;
        width: 24px;
        height: 24px;
        right: 10px;
        top: 10px;
        background: url('../img/sprite.png') -595px -42px no-repeat;
    }
    .switch-language {
        display: none;
    }
    .container_scroll_pos {
        flex-wrap: wrap;
    }
    .container_scroll-nav {
        display: none !important;
    }
    .main__container-row-communication {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 15px;
        padding-top: 65px;
        flex-wrap: wrap;
    }
    .search__entry-field {
        max-width: 425px;
        box-sizing: border-box;
        width: 200px;
    }
    .main__logo {
        display: none;
    }
    .footer {
        max-width: 425px;
        min-width: 320px;
    }
    .container_pos_footer {
        flex-wrap: wrap;
    }
    .main__make-request_popup {
        max-width: 425px;
        min-width: 320px;
    }
    .main__information-about-us {
        max-width: 425px;
        min-width: 320px;
    }
    .main__information-about-us:before {
        max-width: 425px;
        min-width: 320px;
    }
    .main__container-row-areas-work {
        max-width: 425px;
        min-width: 320px;
    }
    .customer-services__scroll-services-clients {
        display: block;
    }
    .make-request__request-form {
        flex-wrap: wrap;
    }
    .container-request-form__entry-field {
        width: 425px;
    }
    .container__news {
        flex-wrap: wrap;
    }
    .shell-news__img-news_main {
        max-width: 425px;
        min-width: 320px;
    }
    .main {
        min-width: 425px;
        min-width: 320px;
    }
    .nav_header {
        position: fixed;
        width: 60%;
        height: 100%;
        box-sizing: border-box;
        top: 0;
        left: 0;
        z-index: 15;
        background-color: $header-color;
        padding-left: 15px;
        padding-top: 40px;
        display: none;
    }
    .nav__link {
        margin-right: 0;
        font-size: 14px;
        display: inline-block;
        width: 100%;
        line-height: 30px;
    }
    .nav__link_active::after {
        content: none;
    }
    .nav_header::after {
        z-index: 5;
        top: 0;
        right: 0;
        position: fixed;
        width: 40%;
        height: 100%;
        background-color: rgba(0,0,0,0.5);
    }
    .button_nav_header {
        display: inline-block;
        margin-top: 30px;
    }
    .communication_mob-hidden {
        display: none;
    }
    .main__request-call {
        display: none;
    }
    
    .switch-language_mob-show {
        display: flex;
        align-items: center;
    }
    .container-position__information {
        font-size: 20px;
        line-height: 24px;
        text-align: center;
    }
    .container-row-information__container-position {
        margin-top: 20px;
        position: static;
    }
    .information__allotment {
        width: 290px;
        margin: 0 auto;
        font-size: 16px;
        line-height: 22px;
        padding: 10px 0;
        margin-top: 15px;
    }
    .main__container-row-information {
        height: auto;
        display: flex;
        justify-content: center;
    }
    .main__bg-img {
        height: 380px;
        background: url('../img/main-mob-bg.jpg') 50% 50% no-repeat;
        background-size: cover;
    }
    .button_information-mob {
        position: absolute;
        bottom: -43.5px;
        left: 50%;
        transform: translateX(-50%);
    }
}